<script>
import publicStyleVue from "../../../public/publicStyle.vue";
import { getArticleListAPI } from "@/api/homeAPI/articleslist";
export default {
  name: "PersonalHeader",
  data() {
    return {
      user: {
        background_photo: "http://dummyimage.com/400x400",
        header_photo: "http://dummyimage.com/400x400",
        user_name: "用户姓名",
        sign: "用户个人签名用户个人签名",
        follow_count: 0,
        follower_count: 0,
        total_like: 0,
        total_collect: 0,
        total_artcile: 0,
      },
      user_id: 0,
      linePosition: 0,
      positionArray: [0, 120, 237, 355, 470],
      lineCnt: 1,
      routeMenu: ["MyMain", "MyArticles", "MyCollection", "MyInfo", "MyFocus"],
      className: [
        "behovered",
        "behovered",
        "behovered",
        "behovered",
        "behovered",
      ],
      initClassName: [
        "behovered",
        "behovered",
        "behovered",
        "behovered",
        "behovered",
      ],
    };
  },
  computed: {
    background_photo() {
      return this.user.background_photo;
    },
    header_photo() {
      return this.user.header_photo;
    },
    username() {
      return this.user.user_name;
    },
    selfintroduce() {
      return this.user.sign;
    },
    focus() {
      return this.user.follow_count;
    },
    fans() {
      return this.user.follower_count;
    },
    like_num() {
      return this.user.total_like;
    },
    collect_num() {
      return this.user.total_collect;
    },
    article_num() {
      return this.user.total_artcile;
    },
  },
  watch: {
    $route: {
      handler: function (newV, _) {
        this.className = this.initClassName.slice(0);
        const cnt = this.routeMenu.indexOf(newV.matched[1].name);
        this.className[cnt] = "behovered beclicked";
        this.linePosition = this.positionArray[cnt];
        this.lineCnt = cnt + 1;
      },
      deep: true,
      immediate: true,
    },
  },
  methods: {
    push(num) {
      this.$router.push({ name: this.routeMenu[num - 1] });
    },
    moveline(num) {
      this.linePosition = this.positionArray[num - 1];
    },
  },
  components: { publicStyleVue },
  mounted() {
    if (localStorage.getItem("user_info")) {
      this.user.user_name = JSON.parse(
        localStorage.getItem("user_info")
      ).user_name;
      this.user.sign = JSON.parse(localStorage.getItem("user_info")).user_sign;
      this.user.header_photo = JSON.parse(
        localStorage.getItem("user_info")
      ).user_head;
      this.user.background_photo = JSON.parse(
        localStorage.getItem("user_info")
      ).user_background;
      this.user.user_name = JSON.parse(
        localStorage.getItem("user_info")
      ).user_name;
    }
    getArticleListAPI().then((res) => {
      if (res.data) {
        res.data.forEach((item) => {
          if (item.author_name == this.user.user_name) {
            this.user.total_artcile++;
          }
        });
      }
    });
  },
};
</script>

<style scoped>
